import React from "react";
import { motion } from "framer-motion";
import cx from "classnames";

import styles from "./diagonal.scss";

export default ({ className, deg = -3, ...rest }) => {
  return (
    <motion.div
      variants={{
        initial: { skewY: 0, marginTop: 0, marginBottom: 0 },
        animate: { skewY: deg }
      }}
      style={{
        marginTop: -Math.abs(deg) + "vw",
        marginBottom: -Math.abs(deg) + "vw"
      }}
      initial="initial"
      animate="animate"
      exit="initial"
      className={cx(styles.wrap, className)}
      {...rest}
    ></motion.div>
  );
};
